.message_container {
    padding: 10px;
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.left {
    display: flex;
    min-width: 5%;
    justify-content: flex-start;
    background-color: #fff;
    padding: 10px;
}

.right {
    display: flex;
    min-width: 5%;
    justify-content: flex-end;
    background-color: rgb(141, 197, 141);
    float: right;
    padding: 10px;
}