.wrapper {
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: hidden;
}

.header {
    height: 50px;
    width: 100%;
    background-color: rgba(102, 167, 5, 0.486);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;

}

.header .avatar  {
    height: 40px;
    width: 40px;
    border-radius: 20px;
    background-color: #fff;
}   

.header .username_container {
    flex: 1;
    margin-left: 10px;
}

.header .username_container .username {
    font-size: 15px;
    font-weight: bold;
}

.chat_container {
    flex: 1;
    background-color: lightgray;
    overflow: auto;
}

.chat_footer {
    height: 50px;
    width: 100%;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.form_container {
    flex: 1;
    width: 100%;
    height: 100%;
}

.form_container .form_input {
    width: 100%;
    height: 100%;
}

.send_button {
    width: 40px;
    height: 40px;
    border-radius: 30px;
    background-color: green;
    margin-left: 10px;
    cursor: pointer;
}